<script>
	export default {
		components: {},
		data() {
			return {
				asdsja: 0,
				arr: "a",
				falg: false,
				languagefalg: false,
				languageList: ['a', 'b', 'c', 'd', 'e'],
				activeLi: "",
				fifth: false,
				fourthly: false,
				thirdly: false,
				second: false,
				firstly: false,
				languagefalgfu: false,
				languagefalg: false,
				a: false,
				list: [{
						time: '2019-12-18 16:20:34',
						img: require("../assets/home/a1.png")
					},
					{
						time: '2019-12-18 16:20:34',
						img: require("../assets/home/a2.png")
					},
					{
						time: '2019-12-18 16:20:34',
						img: require("../assets/home/a3.png")
					},

				],
				liststwo: [{
						time: '2019-12-18 16:20:34',
						img: require("../assets/home/a4.png")
					},
					{
						time: '2019-12-18 16:20:34',
						img: require("../assets/home/a5.png")
					},
					{
						img: require("../assets/home/a6.png")
					},
				],
				lists: [{
						time: '2019-12-18 16:20:34',
						img: require("../assets/home/q1.png")
					},
					{
						time: '2019-12-18 16:20:34',
						img: require("../assets/home/q2.png")
					},
					{
						time: '2019-12-18 16:20:34',
						img: require("../assets/home/a3.png")
					},
				],
				data: this.$t('ttx'),
				scrollW: 0,
				scrollWs: 0,
				scrollWstwo: 0,
				HomeFlag: false,
				ExplorerFlag: false,
				TokenFlag: false,
				ChainFlag: false,
				TestFlag: false,
				scrollTime: null,
				scrollTimes: null,
				tops: ""
			}
		},

		mounted() {
			const top = this.$refs.myDiv.offsetTop;
			console.log(top);
			this.tops = top

			window.addEventListener('scroll', this.handleScroll)

			if (this.$i18n.locale == 'en') {
				this.arr = 'a'
			} else if (this.$i18n.locale == 'zh-CN') {
				this.arr = 'b'
			} else if (this.$i18n.locale == 'Korean') {
				this.arr = 'c'
			} else if (this.$i18n.locale == 'Korean') {
				this.arr = 'd'
			} else if (this.$i18n.locale == 'Japanese') {
				this.arr = 'e'
			}

			this.scroll()
			this.scrolls()
			this.scrollstwo()
			this.animation()
		},
		methods: {
			animation() {

				const numbers = document.querySelectorAll('.section-divstt')
				numbers.forEach(counter => {
					counter.innerText = 0;
					const upDateNumber = () => {
						const target = Number(counter.getAttribute('data-target'))
						const d = Number(counter.innerText)
						const increment = target / 100
						if (d < target) {
							counter.innerText = `${Math.ceil(d + increment)}`
							setTimeout(upDateNumber, 50)
						} else {
							counter.innerText = target
						}
					}
					upDateNumber()
				})
			},
			handleScroll() {
				this.$nextTick(() => {
					var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body
						.scrollTop
					if (scrollTop > 0) {
						document.getElementById('scrollTopBg').style.backgroundColor = '#02088B'
					} else {
						document.getElementById('scrollTopBg').style.backgroundColor = 'rgba(2, 8, 139, 0)'
					}
					if (this.tops > scrollTop) {
						this.animation()
					}
				});
				this.$forceUpdate()
			},
			index(i) {
				if (i == 1) {
					this.$router.push({
						path: '/'
					})
				} else {
					this.$router.push({
						path: '/dapp'
					})
				}
			},
			navLink() {
				this.$router.push({
					path: '/dapp'
				})
			},

			clickTo() {
				this.$i18n.locale = 'en'
			},
			cli() {
				this.falg = !this.falg
			},
			stopScrolls() {
				var target = this.$refs.contlists;
				clearInterval(this.scrollTime)
			},

			startScrolls() {
				var target = this.$refs.contlists;
				this.scrollWs = target.offsetLeft;
				this.scrolls()
			},

			scrolls() {
				var that = this;
				var target = this.$refs.contlists;
				var initLeft = 0;
				if (this.scrollWs < 0) {
					initLeft = this.scrollWs * (-1)
				}
				this.scrollTime = setInterval(function() {
					initLeft++;
					if (initLeft >= target.offsetWidth / 2) {
						initLeft = 0;
					}
					target.style.left = '-' + initLeft + 'px';

				}, 16)
			},






			stopScrollstwo() {
				var target = this.$refs.contliststwo;
				clearInterval(this.scrollTimes)
			},
			startScrollstwo() {
				var target = this.$refs.contliststwo;
				this.scrollWstwo = target.offsetRight;
				this.scrollstwo()
			},
			scrollstwo() {
				var that = this;
				var target = this.$refs.contliststwo;
				var initRight = 0;

				if (this.scrollWstwo < 0) {
					initRight = this.scrollWstwo * (-1)
				}
				this.scrollTimes = setInterval(function() {
					initRight++;
					if (initRight >= target.offsetWidth / 2) {
						initRight = 0;
					}
					target.style.right = '-' + initRight + 'px';

				}, 16)
			},





			stopScroll() {
				var target = this.$refs.contlist;
				clearInterval(this.scrollTime)
			},
			startScroll() {
				var target = this.$refs.contlist;
				this.scrollW = target.offsetLeft;
				this.scroll()
			},
			scroll() {
				var that = this;
				var target = this.$refs.contlist;
				var initLeft = 0;
				if (this.scrollW < 0) {
					initLeft = this.scrollW * (-1)
				}
				this.scrollTime = setInterval(function() {
					initLeft++;
					if (initLeft >= target.offsetWidth / 2) {
						initLeft = 0;
					}
					target.style.left = '-' + initLeft + 'px';

				}, 16)
			},

			findCurrentIndex(scrollLeft) {
				var that = this
				var items = that.list.getElementsByTagName("li");
				for (var i = 0; i < items.length; i++) {
					var item = items[i];
					var itemLeft = item.offsetLeft;
					var itemRight = itemLeft + item.offsetWidth;
					if (scrollLeft >= itemLeft && scrollLeft <= itemRight) {
						that.asdsja = ''
						return i
					} else {}
				}
				return -1;
			},
			clicks(e) {
				console.log(e)
				this.arr = e
				if (this.arr == 'a') {
					this.$i18n.locale = 'en'
				} else if (this.arr == 'b') {
					this.$i18n.locale = 'zh-CN'
				} else if (this.arr == 'c') {
					this.$i18n.locale = 'Korean'
				} else if (this.arr == 'd') {
					this.$i18n.locale = 'Russian'
				} else if (this.arr == 'e') {
					this.$i18n.locale = 'Japanese'
				}
				this.languagefalgfu = false
				this.languagefalg = false
				this.data = this.$t('ttx')
				console.log(this.$i18n.locale)
				localStorage.setItem('languageConverter', this.$i18n.locale)
				this.$forceUpdate()
			},
			clickTo() {
				this.$i18n.locale = 'en'
			},
			cli() {
				this.falg = !this.falg
			}
		}
	}
</script>

