<script>
	export default {
		components: {},
		data() {
			return {
				arr: "a",
				falg: false,
				HomeFlag:false,
				ExplorerFlag:false,
				TokenFlag:false,
				ChainFlag:false,
				TestFlag:false,
				languagefalg:false,
				languagefalgfu:false,
				languageList:['a','b','c','d','e']
			}
		},

		mounted() {
			if (localStorage.getItem('languageConverter')) {

			} else {
				localStorage.setItem('languageConverter', 'en')
			}
			if (this.$i18n.locale == 'en') {
				this.arr = 'a'
			} else if (this.$i18n.locale == 'zh-CN') {
				this.arr = 'b'
			} else if (this.$i18n.locale == 'Korean') {
				this.arr = 'c'
			} else if (this.$i18n.locale == 'Korean') {
				this.arr = 'd'
			} else if (this.$i18n.locale == 'Japanese') {
