// router.js
import Vue from 'vue';
import VueRouter from 'vue-router';
import Index from './view/Index.vue';
import crowdFunding from './view/crowdFunding.vue';
import detile from './view/detile.vue';
Vue.use(VueRouter);

const

	routes = [{
			path: '/',
			component: crowdFunding,
		},
		{
			path: '/DAPP',
			component: detile,
		}
	];

const router = new VueRouter({
	routes
});

export default router;